// DataDisplayArea.tsx
import React, { useState, useEffect } from "react";
import UIPanel from "./UIPanel";
import { Bar } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import { fetchEstateTransactionData } from "../api";
import { EstateTransactionResponse } from "../types";

// Chart.js
ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

const DataDisplayArea: React.FC = () => {
  const [prefCode, setPrefCode] = useState<number>(1);
  const [prefName, setPrefName] = useState<string>("");
  const [selectedYear, setSelectedYear] = useState<number>(2009);
  const [displayType, setDisplayType] = useState<number>(1);
  const [priceData, setPriceData] = useState<EstateTransactionResponse | null>(
    null
  );
  const [estateData, setEstateData] =
    useState<EstateTransactionResponse | null>(null);
  const [averagePrice, setAveragePrice] = useState<number>(0);
  const [error, setError] = useState<string | null>(null);

  // 
  useEffect(() => {
    const cacheKey = `${prefCode}-${displayType}`; // prefCodedisplayType

    // localStorage
    const cachedData = localStorage.getItem(cacheKey);
    if (cachedData) {
      const parsedData: EstateTransactionResponse = JSON.parse(cachedData);
      setEstateData(parsedData);
      setError(null); // 
      console.log(":", parsedData);
      console.log("estateData:", estateData);

      // 
      calculateAveragePrice();
      return; // API
    }

    const fetchAndCacheData = async () => {
      try {
        // 
        const data = await fetchEstateTransactionData(prefCode, displayType);
        setEstateData(data);

        // localStorage
        localStorage.setItem(cacheKey, JSON.stringify(data));
        console.log("DATA:", data);

        // 
        calculateAveragePrice();
      } catch (err) {
        console.error(":", err);
        setError("");
      }
    };

    fetchAndCacheData();
  }, [prefCode, displayType]);

  const calculateAveragePrice = () => {
    console.log("");
    const allPrices: number[] = [];

    // localStorage
    for (let i = 1; i <= 2; i++) {
      // 147
      const key = `${i}-${displayType}`; // 
      const cachedData = localStorage.getItem(key);
      if (cachedData) {
        const parsedData: EstateTransactionResponse = JSON.parse(cachedData);
        const value = parsedData.years.find(
          (year) => year.year === selectedYear
        )?.value;
        if (value) {
          allPrices.push(value);
        }
      }
    }

    // 
    const totalValue = allPrices.reduce((sum, price) => sum + price, 0);
    const avgPrice = allPrices.length > 0 ? totalValue / allPrices.length : 0; // 
    console.log("", avgPrice);
    setAveragePrice(avgPrice);
    setError(null);
  };

  // 
  const handleYearChange = (year: number) => {
    setSelectedYear(year);
  };

  // 
  const handlePrefChange = (prefCode: number) => {
    setPrefCode(prefCode);
  };

  const handlePrefNameChange = (prefName: string) => {
    setPrefName(prefName);
  };

  // 
  const handleDisplayTypeChange = (type: number) => {
    setDisplayType(type);
  };

  const displayTypeText =
    displayType === 1
      ? "()"
      : displayType === 2
      ? "()"
      : displayType === 3
      ? ""
      : displayType === 4
      ? ""
      : displayType === 5
      ? ""
      : "";

  // 
  const chartData = {
    labels: [prefName, ""],
    datasets: [
      {
        label: " (/)",
        // selectedYear 
        data: [
          estateData
            ? estateData.years.find((year) => year.year === selectedYear)
                ?.value || 0
            : 0,
          averagePrice,
        ],
        backgroundColor: ["#4C9F70", "#FF6347"],
      },
    ],
  };

  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        display: false, // 
      },
    },
    scales: {
      y: {
        title: {
          display: true,
          text: "/",
          font: {
            size: 12,
          },
        },
        grid: {
          drawOnChartArea: false, // 
          color: "rgba(255, 255, 255, 1)", // 
          lineWidth: 1, // 
        },
        ticks: {
          font: {
            size: 10,
          },
        },
        border: {
          color: "rgba(255, 255, 255, 1)", // Y
          width: 1, // Y
        },
      },
      x: {
        ticks: {
          font: {
            size: 10,
          },
        },
        border: {
          color: "rgba(255, 255, 255, 1)", // X
          width: 1, // X
        },
      },
    },
  };

  return (
    <div className="flex flex-col p-4">
      {/*  */}
      <div className="mb-4">
        <h2 className="text-2xl font-bold flex items-center">
          <span className="mr-2"></span>
          
        </h2>
        <p className="text-sm mt-1">1</p>
        <hr className="my-2 border-gray-600" />
      </div>

      <div className="flex flex-col sm:flex-row justify-between mt-4 items-center">
        <div className="flex-grow flex justify-center">
          {error ? (
            <p className="text-red-500">{error}</p>
          ) : (
            <div style={{ width: "50%", height: "500px" }}>
              <div className="text-center mb-2">
                <span className="text-lg font-semibold">
                  {prefName} {selectedYear} {displayTypeText}
                </span>
              </div>
              <Bar data={chartData} options={chartOptions} />
            </div>
          )}
        </div>
        <div className="w-full sm:w-1/4">
          {" "}
          {/* UIPanel */}
          <UIPanel
            prefCode={prefCode}
            selectedYear={selectedYear}
            displayType={displayType}
            handlePrefChange={handlePrefChange}
            handlePrefNameChange={handlePrefNameChange}
            handleYearChange={handleYearChange}
            handleDisplayTypeChange={handleDisplayTypeChange}
          />
        </div>
      </div>
    </div>
  );
};

export default DataDisplayArea;
